/**
 * 
 */
export enum MethodType{
    GET,
    POST,
    DELETE,
    PUT,
    HEAD
}

/**
 * 
 */
export interface IJpp {
    set?: {[id: string]: string},
    copy?: {[id: string]: string},
    move?: {[id: string]: string},
    del?: Array<string>
}

/**
 * 
 */
export interface IInterfaceModel {
    // 
    key: string;
    // 
    method: MethodType;
    // 
    address: string;
    // 
    port?: number;
    // 
    path: string;
    // key
    serverKey?: string;
    // ,false 
    params?: boolean|Object;
    // restful
    isRestful: boolean;
    // 
    jpp?: IJpp;
    // header
    header?: IJpp;
    // putdelete
    idFieldPath?: string;
    // 
    config?: Object;
}


